.dashboard-root-container {
  background: #f9f9f9;
  padding: 0 0 48px 0;
  min-height: 100vh; }

.bottom-nav {
  text-rendering: optimizeLegibility;
  direction: ltr;
  text-align: left;
  margin: 0;
  height: 48px;
  max-height: 48px;
  background: rgba(0, 0, 0, 0.8);
  user-select: none;
  backdrop-filter: saturate(180%) blur(20px); }
  .bottom-nav a {
    color: #fff; }
  .bottom-nav.light {
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11), 0 5px 15px 0 rgba(0, 0, 0, 0.08); }
    .bottom-nav.light a {
      color: #8795a1; }
      .bottom-nav.light a.router-link-active {
        color: #35aa35; }

.ios .bottom-nav.light {
  background: rgba(255, 255, 255, 0.85); }
